import React from 'react';
import Layout from '../components/Layout';
import {Image, Container, Card, Icon} from 'semantic-ui-react';

export default () => {
  return (
    <Layout>
      <Image src='https://raw.githubusercontent.com/jdleo/Williams-Tank-Lines/master/assets/careersImg.png'/>
      <Image.Group size='large' style={{'marginTop': '40px'}}>
        <Image src="https://raw.githubusercontent.com/jdleo/Williams-Tank-Lines/master/assets/careers/careersImg2.png" rounded/>
        <Image src="https://raw.githubusercontent.com/jdleo/Williams-Tank-Lines/master/assets/careers/careersImg3.png" rounded/>
      </Image.Group>
      <Container style={{'marginTop': '40px'}}>
        <Card.Group centered>
          <Card fluid>
            <Card.Content>
              <Card.Header>Driver Opportunities</Card.Header>
              <Card.Meta>
                <span className='date'>This position requires the loading, transporting, and delivering of petroleum products and operation of company equipment in a safe manner, complying with all state, federal, company and customer policies and procedures.</span>
              </Card.Meta>
              <Card.Description>Matthew is a musician living in Nashville.</Card.Description>
            </Card.Content>
            <Card.Content extra>
              <a>
                <Icon name='user' />
                22 Friends
              </a>
            </Card.Content>
          </Card>
        </Card.Group>
      </Container>
    </Layout>
  );
};



